axios.defaults.timeout = 150000;
axios.defaults.baseURL = 'http://cwcwx.wfust.edu.cn';
axios.interceptors.request.use(function (config) {
    if (config.method === 'post') {
        config.data = qs.stringify(config.data);
    }
    if (sessionStorage.getItem('token')) {
        config.headers.common['token'] = sessionStorage.getItem('token'); //携带token
    }
    return config;
}, function (err) {
    return _Promise.reject(err);
});

// http response 拦截器
axios.interceptors.response.use(function (response) {
    // console.log(response.data.code)
    if (response.data.code == 115) {
        _this.$message({
            showClose: true,
            message: "未登录",
            type: 'success'
        });
        router.push('Login');
    } else {
        return response.data;
    }
}, function (error) {
    if (error.response) {
        switch (error.response.status) {
            case 401:
                store.commit(types.logout);
                router.currentRoute.path !== 'Login' && router.replace({
                    path: 'Login',
                    query: { redirect: router.currentRoute.path }
                });
        }
    }
    return _Promise.reject(error.response);
});

